import React, { Component } from 'react';
import './index.css';
import LngSupport from '../Lng/LngSupport.js';
import Lng from '../Lng/Lng.js';
import Header from '../Header';
import Footer from '../Footer';
import AboutMe from '../AboutMe';
import SocialHub from '../SocialHub';
import Project from '../Project';
import FeedbackForm from '../FeedbackForm';
import imgASC from './assets/ASC_ICON.webp';
import imgERI from './assets/ERI_icon.webp';
import imgXDA from './assets/xda-flat-circle-icon-256.webp';
import imgReactJS from './assets/Reactlogo512.png';

export default class Main extends Component {

    state = {
        CurrentLng: "en", 
    };

    lngDetect = () => {
        if (!this.state.hasOwnProperty("UserLng")) {
            this.state.UserLng = navigator.language.substr(0, 2) || navigator.userLanguage.substr(0, 2);
            if (LngSupport.hasOwnProperty(this.state.UserLng)) {
                if (LngSupport[this.state.UserLng].autouse) {
                    this.state.CurrentLng = this.state.UserLng;
                };
            };
        };
    };

    lngLoad = () => {
        if (Lng.hasOwnProperty(this.state.CurrentLng)) {
            this.state.Lng = Lng[this.state.CurrentLng];
        } else {console.log("Main-lngLoad()", " : '"+this.state.CurrentLng+"' not found in Lng.js!")};    
    };

    MainSetState = (value) => {
        this.setState(value);
    };

    render() {
        this.lngDetect();
        this.lngLoad();
        return(
            <div className="main">
                <Header MainState={this.state} SetState={this.MainSetState}/>
                <AboutMe MainState={this.state} />
                <SocialHub />
                <div className="portf-text"><span>{this.state.Lng.MainTextMyWorks}</span></div>
                <Project img={imgASC}
                        prj_text={this.state.Lng.ProjectASCText} 
                        ablock={true}/>
                <Project img={imgERI} 
                        prj_text={this.state.Lng.ProjectERIText} />
                <Project img={imgXDA}
                        prj_text={this.state.Lng.ProjectXDAText}
                        ablock={true} />
                <Project img={imgReactJS}
                        prj_text={this.state.Lng.ProjectReactSiteText}
                        ablock={false} />
                <FeedbackForm MainState={this.state}/>
                <Footer MainState={this.state} />
            </div>
        );
    };
};