import React, { Component } from 'react';
import './index.css';
let ablock_css;

export default class Project extends Component {
    
    render() {
        const { img = "", prj_text = "", ablock = false} = this.props;

        if (ablock) {ablock_css = "project another-block" } else {ablock_css = "project" };

        return(
            <div className={ablock_css}>
                <img src={img} className="project-img" alt="" />
                <p className="project-text">{prj_text}</p>
            </div>
        );
    };
};