import React, { Component } from 'react';
import LngSupport from '../Lng/LngSupport.js';
import './index.css';

export default class Header extends Component {

    render() {
        const { MainState } = this.props;
        const LngOptions = [];

        for (var key in LngSupport) {
            LngOptions.push(
            <option className="header-lang-option" key={LngSupport[key].id} value={key} selected={MainState.CurrentLng === key ? true:false}>{LngSupport[key].name}</option>
            );
        }

        return(
            <div className="header">
                  <div className="header-title-div"> 
                      <span className="header-title-text">Octanium Software</span>
                  </div>
                  <div className="header-lang-menu">
                <span className="header-lang-span">{MainState.Lng.HeaderLanguage}</span>
                    <select className="header-lang-selector" onChange={(event) => {this.props.SetState({CurrentLng: event.target.value,})}} size="1">
                        {LngOptions}
                    </select>
                  </div>
            </div>
        );
    };
};