import React, { Component } from 'react';
import './index.css';

export default class FeedbackForm extends Component {

    api_telegram_send = (message, btnTextSend) => {
        // use: 
        //  command: api_telegram_send("Hello!, my phone: XXXXXX");
        //  send in telegram:
        //      Hello, my phone: XXXXXX 
        //
        //  OR (if you need segue on new lines)
        //
        //  command: api_telegram_send(["Hello!", "my phone: XXXXXX"]);
        //  send in telegram:
        //      Hello
        //      my phone: XXXXXX 

        try {
            var text = "<b>Host: "+window.location.hostname+"</b>%0A";
        } catch {
            var text = "";
        }
        if (typeof message == "object") {
            message.forEach(function(item, i, message) {
                text = text+"<b>"+item+"</b>%0A";
            });
        };
        if (typeof message == "string") {
            text = text+message;
        };
        if (text !== "") {
            (async () => {
                try {
                    const response = await fetch("https://api.telegram.org/bot915175063:AAHGcx3PJNXCE3f5uqHsW-QnK3hDOdktHf0/sendMessage?chat_id=122785894&parse_mode=html&text="+text, { 
                        method: 'POST',});
                    if (response.ok) {
                        // ok
                        document.getElementById("btn_submit").value = btnTextSend;
                        document.getElementById("btn_submit").disabled = false;
                    } else {
                        console.log("api_telegram_send_error:", response.status);
                    }
                } catch (error) {
                    console.log("api_telegram_send_error:", error)
                }
            })();
        } else {
            console.log("api_telegram_send_error:", "'message' is empty! use type: object or string.")
        };
    };

    SendMess = (btnTextSend, btnTextSending) => {
        const input_name = document.getElementById("input_name");
        const input_telephone = document.getElementById("input_telephone");
        const input_message = document.getElementById("input_message");
        var validae_send = true;
        if (input_name.value === "") {
            validae_send = false;
        }
        if (input_telephone.value === "") {
            validae_send = false;
        }
        if (input_message.value === "") {
            validae_send = false;    
        }
        if (validae_send) {
            document.getElementById("btn_submit").disabled = true;
            document.getElementById("btn_submit").value = btnTextSending;
            this.api_telegram_send([input_name.value, input_telephone.value, input_message.value], btnTextSend);
            input_name.value = "";
            input_telephone.value = "";
            input_message.value = "";
        };
    };

    render() {
        const { MainState } = this.props;

        return(
            <div className="feedback-form">
                <div className="feedback-form-form">
                    <p className="feedback-form-text-title">{MainState.Lng.FeedbackFormTextTitle}</p>
                    <form>
                        <div class="input-group mb-3 feedback-form-divs">
                            <div class="input-group-prepend">
                                <span class="input-group-text" id="inputGroup-sizing-default">{MainState.Lng.FeedbackFormFieldUser}</span>
                            </div>
                            <input id="input_name" type="text" class="form-control" aria-label="Default" aria-describedby="inputGroup-sizing-default" required></input>
                        </div>
                        <div class="input-group mb-3 feedback-form-divs">
                            <div class="input-group-prepend">
                                <span class="input-group-text" id="inputGroup-sizing-default">{MainState.Lng.FeedbackFormFieldTel}</span>
                            </div>
                            <input id="input_telephone" type="tel" class="form-control" aria-label="Default" aria-describedby="inputGroup-sizing-default" required></input>
                        </div>  
                        <div class="input-group mb-3 feedback-form-divs">
                            <div class="input-group-prepend">
                                <span class="input-group-text" id="inputGroup-sizing-default">{MainState.Lng.FeedbackFormFieldMess}</span>
                            </div>
                            <input id="input_message" type="text" class="form-control" aria-label="Default" aria-describedby="inputGroup-sizing-default" required></input>
                        </div> 
                        <input id="btn_submit" class="btn btn-light" type="submit" onClick={() => {this.SendMess(MainState.Lng.FeedbackFormbtnSend, MainState.Lng.FeedbackFormbtnSending)}} value={MainState.Lng.FeedbackFormbtnSend}></input>
                    </form>
                </div>
            </div>
        );
    };
};