import React, { Component } from 'react';
import Reactlogo from './assets/Reactlogo.svg';
import './index.css';

export default class Footer extends Component {

    render() {
        const { MainState } = this.props;

        return(
            <div className="footer">
                <div className="footer-createdOnReact">
                    <p className="footer-crtext">{MainState.Lng.FooterMadeOnReact} <img src={Reactlogo} className="footer-react-logo" alt="logo" /> React JS</p>
                </div>
            </div>
        );
    };
};